<template>
  <div id="PortfolioList" class="portfolio-list">
    <Header />
    <h1 class="portfolio-title">portfolio</h1>

    <div class="list-center">
      <div class="tag-area">
        <button :class="tagClass(t)" @click="setSelectTag(t)" v-for="t in portfolioTag" :key="t">{{ t }}</button>
      </div>

      <a
        class="list-card"
        v-for="p in filterPortfolioList"
        :key="p.Serial"
        :href="goPortfolioPage(p.Serial)"
        data-aos="fade-up"
        :data-aos-duration="AOS.Dur"
        :data-aos-offset="AOS.Offset"
      >
        <img class="card-img" :src="getPhoto(p.Img[0])" />
        <div class="card-hash-tag-area">
          <label class="card-hash-tag" v-for="t in p.Tag" :key="p.Serial + t">
            {{ `#${t}` }}
          </label>
        </div>
        <p class="card-title">
          {{ p.Name }}
        </p>
      </a>
      <div v-if="filterPortfolioList.length == 0" class="list-no-card">
        此篩選組合查無資料, 請重新選取
      </div>
      <div class="clear-both"></div>
    </div>
    <Footer />
  </div>
</template>

<script>
import Header from '@/components/header/Header.vue';
import Footer from '@/components/footer/Footer.vue';
import portfolioData from '@/json/portfolioData.json';
import AOS from 'aos';
import 'aos/dist/aos.css';

export default {
  mounted() {
    AOS.init();
  },
  data() {
    return {
      AOS: {
        Offset: '0',
        Dur: '1200',
      },
      portfolioList: portfolioData.Info,
      portfolioTag: portfolioData.Tag,
      filterTag: ['All'],
    };
  },
  computed: {
    filterPortfolioList: function() {
      if (this.filterTag[0] == 'All') {
        return this.portfolioList;
      } else {
        return this.portfolioList.filter(item => {
          let chkRes = true;
          for (let i = 0; i < this.filterTag.length; i++) {
            if (item.Tag.indexOf(this.filterTag[i]) < 0) {
              chkRes = false;
              break;
            }
          }
          return chkRes;
        });
      }
    },
  },
  methods: {
    getPhoto(url) {
      return require('@/assets/img/portfolio/' + url);
    },
    goPortfolioPage(serial) {
      return `/page/Portfolio?s=${serial}`;
    },
    tagClass(tag) {
      return this.filterTag.indexOf(tag) >= 0 ? 'tag-select' : 'tag-unselect';
    },
    setSelectTag(tagName) {
      if (tagName == 'All') {
        this.filterTag = ['All'];
      } else if (this.filterTag[0] == 'All') {
        this.filterTag = [tagName];
      } else if (this.filterTag.indexOf(tagName) >= 0) {
        let indexOf = this.filterTag.indexOf(tagName);
        this.filterTag.splice(indexOf, 1);
      } else {
        this.filterTag.push(tagName);
      }
      if (this.filterTag.length == 0) {
        this.filterTag = ['All'];
      }
    },
  },
  components: { Header, Footer },
};
</script>
